"use client";

import React, { useState } from "react";
import { useParams } from "next/navigation";
import ExamProgressBar from "../../../components/ExamProgressBar";
import ExamQuestion from "../../../components/ExamQuestion";
import ExamResults from "../../../components/ExamResults";
import questionsData from "../../data/questions";
import BackButton from "../../../components/BackButton";

const ExamPage = () => {
  const { id } = useParams();
  const moduleId = Number(id);

  if (isNaN(moduleId)) {
    return <p className="text-center text-red-500">ID del módulo no válido.</p>;
  }

  console.log("Módulo ID:", moduleId);
  console.log("Preguntas para el módulo:", questionsData[moduleId]);

  const allQuestions = questionsData[moduleId] || [];
  const totalQuestions = allQuestions.length;

  if (totalQuestions === 0) {
    return <p className="text-center text-red-500">No hay preguntas disponibles para este módulo.</p>;
  }

  const maxQuestions = 10;
  const randomQuestions = totalQuestions > 0 
    ? allQuestions.sort(() => 0.5 - Math.random()).slice(0, Math.min(maxQuestions, totalQuestions)) 
    : [];

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState<string[]>([]);
  const [showResults, setShowResults] = useState(false);

  const handleAnswer = (answer: string) => {
    setAnswers([...answers, answer]);

    if (currentQuestionIndex + 1 < randomQuestions.length) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      setShowResults(true);
    }
  };

  const currentQuestion = randomQuestions[currentQuestionIndex] || null;

  return (
    <div className="p-6 bg-background min-h-screen">
      <BackButton onClick={() => window.history.back()} />

      {showResults ? (
        <ExamResults
          totalQuestions={randomQuestions.length}
          correctAnswers={answers.filter(
            (answer, i) => answer === randomQuestions[i].correctAnswer
          ).length}
          questions={randomQuestions}
        />
      ) : (
        <>
          <ExamProgressBar current={currentQuestionIndex + 1} total={randomQuestions.length} />

          {currentQuestion ? (
            <ExamQuestion
              question={currentQuestion}
              onAnswer={handleAnswer}
              questionNumber={currentQuestionIndex + 1}
            />
          ) : (
            <p className="text-center text-red-500">No hay más preguntas.</p>
          )}
        </>
      )}
    </div>
  );
};

export default ExamPage;
