"use client";

interface ExamButtonProps {
  isDisabled: boolean;
  onClick: () => void;
  children: React.ReactNode;
}

const ExamButton: React.FC<ExamButtonProps> = ({ isDisabled, onClick, children }) => {
  return (
    <button
      disabled={isDisabled}
      onClick={onClick}
      className={`p-3 rounded-lg font-semibold border-2 border-black ${
        isDisabled ? "bg-gray-400 text-black cursor-not-allowed" : "bg-primary hover:bg-primary-dark text-white"
      }`}
    >
      {children}
    </button>
  );
};

export default ExamButton;
